@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin pc {
    @media (min-width: v.$pc) {
        @content;
    }
}
@mixin pc_grande {
    @media (min-width: v.$pc_grande) {
        @content;
    }
}

@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

@mixin contenedor_md {
    width: 95%;
    max-width: 90rem;
    margin: 0 auto;
}

@mixin contenedor_sm {
    width: 95%;
    max-width: 60rem;
    margin: 0 auto;
}

@mixin heading {
    font-family: v.$fuente-principal;
    font-weight: v.$black;
    margin: 2rem 0;
}

@mixin nombre_pagina {
    @include heading();
    margin: 6rem 0 3rem 0;
    text-align: center;
}

@mixin descripcion_pagina {
    text-align: center;
    font-size: 1.8rem;
    font-weight: v.$delgada;
    color: v.$gris-oscuro;
    margin-bottom: 5rem;
}

@mixin texto_fluido($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw, $max);
}

@mixin texto_degradado($color_inicio, $color_final) {
    background: linear-gradient(to right, $color_inicio, $color_final);
    background-clip: text;
    color: transparent;
}

@mixin logo {
    color: v.$blanco;
    text-align: center;
    margin-bottom: 2rem;
    white-space: nowrap;

    @include pc {
        text-align: left;
        margin: 0;
    }
}

@mixin menu_social($color, $size){

    &__enlace {

        &::before {
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            font-family: "Font Awesome 6 Brands";
        }

        &[href*="facebook.com"]::before{
            content: "\f39e";
        }

        &[href*="github.com"]::before {
            content: "\f09b";
        }

        &[href*="tiktok.com"]::before {
            content: "\e07b";
        }

        &[href*="instagram.com"]::before {
            content: "\f16d";
        }

        &[href*="youtube.com"]::before {
            content: "\f167";
        }

        &[href*="twitter.com"]::before {
            content: "\f099";
        }

        //&[href*="https://x.com"]::before {
        //    content: "\e61b";
        //}
    }

    &__ocultar {
        display: none;
    }
}

@mixin alerta($color){
    background-color: lighten($color, 40%);
    border-left: 0.5rem solid $color;
    color: $color;
}

@mixin enlace{
    all: unset;
    color: v.$blanco;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: v.$bold;
    
    &:hover {
        color: v.$azul;
        cursor: pointer;
    }
}

@mixin sombra {
    box-shadow: inset 0 0 12px rgba(0,0,0,0.2);
}

@mixin reset_lista {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin tags{
    @include reset_lista();
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 1.5rem;

    > li {
        background-color: v.$azul;
        color: v.$blanco;
        padding: 1rem;
        border-radius: 0.5rem;
        font-size: 1.4rem;
        font-weight: bold;

        &:hover {
            cursor: pointer;
        }
    }
}

@mixin tarjeta {
    border: 1px solid lighten(v.$gris, 10%);
    padding: 3rem;
    &__nombre {
        font-weight: v.$black;
        font-size: 2.6rem;
        margin: 2rem 0;
    }
}

@mixin bg_gradient($bg_color){
    background-image: linear-gradient(to left top, $bg_color, lighten($bg_color, 15%));
}