@use "base/variables" as v;
@use "base/mixins" as m;

.speakers {
  &__grid {
    @include m.contenedor;
    @include m.grid(1, 5rem);

    @include m.tablet{
      @include m.grid(2, 5rem);
    }

    @include m.pc_grande{
      @include m.grid(3, 5rem);
    };
  }
}

.speaker {
  @include m.tarjeta;

  background-position: center top;
  background-size: 350px;
  background-repeat: no-repeat;

  @for $i from 1 through 4 {
    &:nth-child(4n + #{$i}) {
      background-image: image-set(
        url("../img/bg_#{$i}.avif") 1x,
        url("../img/bg_#{$i}.webp") 1x,
        url("../img/bg_#{$i}.png") 1x
      );
    };
  };

  &__imagen {
    min-width: 350px;

    @include m.tablet {
      min-width: unset;
    }
  }

  &__ubicacion {
    color: v.$gris;
    margin: 1rem 0;
    font-size: 2rem;
    line-height: 1.5;
  }

  &__listado-skills {
    @include m.tags;

    > li:hover {
      cursor: default;
    }
  }
}

.speaker-sociales {
  @include m.menu_social(v.$negro, 2rem);
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
}